import React, { Component } from "react";
import ProjectCard from "../../components/projectCard/ProjectCard.js";
import "./ProjectAccordion.css";
import { Accordion, Panel } from "baseui/accordion";

class ProjectAccordion extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="project-accord">
        <Accordion>
          {this.props.sections.map((section) => {
            return (
              <Panel
                className="accord-panel"
                title={`${section["title"]} (${section["projects"].length} Project${(section["projects"].length > 1) ? "s" : ""})`}
                key={section["title"]}
                overrides={{
                  Header: {
                    style: () => ({
                      backgroundColor: `${theme.body}`,
                      border: `1px solid`,
                      borderRadius: `5px`,
                      borderColor: `${theme.headerColor}`,
                      marginBottom: `3px`,
                      // marginLeft: '20px',
                      // marginRight: '20px',
                      fontFamily: "Google Sans Regular",
                      color: `${theme.text}`,
                      ":hover": {
                        color: `${theme.secondaryText}`,
                      },
                    }),
                  },
                  Content: {
                    style: () => ({
                      backgroundColor: `${theme.body}`,
                    }),
                  },
                }}
              >
{section["projects"].map((project,index) => {
                  return (
                    <ProjectCard index={index} totalCards={section["projects"].length} project={project} theme={theme} />
                  );
                })}
              </Panel>
            );
          })}
        </Accordion>
      </div>
    );
  }
}

export default ProjectAccordion;
