import React, { Component } from 'react';
import Markdown from "react-markdown";
import remarkGfm from "remark-gfm";
import './ProjectCard.css';
import { Fade } from "react-reveal";

class ProjectCard extends Component {
  render() {
    const project = this.props.project;
    console.log(project)
    const index = this.props.index;
    const totalCards = this.props.totalCards;
    const theme = this.props.theme;
    return (
      <div className="project-list-item" style={{ marginTop: (index === 0 ? 30 : 50) }}>
        <Fade left duration={2000} distance="40px">
          <div className="project-card-logo-div">
            {/* <img
              className="project-card-logo"
              src={require(`../../assests/images/${project["logo_path"]}`)}
              alt=""
            /> */}
          </div>
        </Fade>
        <div className="project-card-stepper">
          <div style={{ width: 20, height: 20, backgroundColor: `${theme.headerColor}`, borderRadius: 50, zIndex: 100 }} />
          {
            index !== (totalCards - 1) &&
            <div style={{ height: 190, width: 2, backgroundColor: `${theme.headerColor}`, position: 'absolute', marginTop: 20 }} />
          }
        </div>
        <Fade right duration={2000} distance="40px">
          <div style={{ display: 'flex', flexDirection: 'row' }}>
            <div className="arrow-left" style={{ borderRight: `10px solid ${theme.body}` }}></div>
            <div className="project-card" style={{ background: `${theme.body}` }}>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                <div>
                  <h3 className="project-card-title" style={{ color: theme.text }}><Markdown remarkPlugins={[remarkGfm]}>{project["title"]}</Markdown></h3>
                  <p className="project-card-company" style={{ color: theme.text }}>
                    <a href={project["company_url"]} target="_blank" rel="noopener noreferrer">
                      {project["company"]}
                    </a>
                  </p>
                </div>
                <div>
                  <div className="project-card-heading-right">
                    <p className="project-card-duration" style={{ color: theme.secondaryText }}>{project["duration"]}</p>
                    <p className="project-card-location" style={{ color: theme.secondaryText }}>{project["location"]}</p>
                  </div>

                </div>
              </div>
              {/* <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: 0 }}> */}
              <div className=".project-description-content-list">
                  {project["descriptions"].map((sentence) => {
                  return (
                    <p style={{ color: theme.text}}>
                      {/* {sentence} */}
                      <Markdown remarkPlugins={[remarkGfm]}>{sentence}</Markdown>
                    </p>
                  );
                })}
                </div>
                </div>
            {/* </div> */}

          </div>
        </Fade>
      </div>

    );
  }
}

export default ProjectCard;
