import React, { Component } from "react";
import Select from "react-select";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import ProjectAccordion from "../../containers/projectAccordion/ProjectAccordion.js";
import "./CustomProjects.css";
import { customProjects } from "../../portfolio.js";
import { Fade } from "react-reveal";
import CustomProjectsImg from "./CustomProjectsImg";


class CustomProjects extends Component {
  checkTag(targetProjects, project){
    if (project["tag_key"]){
      return targetProjects.includes(project["tag_key"])
    }
    return targetProjects.includes(project["title"])
  }

  filterSections(sections, allowedTags, tag) {
    if (tag){
      let targetProjects = [];
      for(let i = 0; i < allowedTags.length; i++){
        if(allowedTags[i]["value"] === tag){
          targetProjects = allowedTags[i]["projects"];
          break;
        }
      }
      for (let i = 0; i < sections.length; i++) {
        sections[i]["projects"] = sections[i]["projects"].filter(project => this.checkTag(targetProjects, project));
      }
      return sections.filter(section => section["projects"].length > 0);
      // return sections
    }
    return sections;
  };

  getDefaultValue(options, tag){
    let found = options.find(e => e["value"] === tag);
    return found;
  }

  handleChange(selectedOption, actionMeta){
    if(actionMeta && actionMeta["action"] === "clear"){
      window.location.href = '/projects';
    }else{
      window.location.href = '/projects?tag=' + selectedOption.value;
    }
  };

  render() {
    const theme = this.props.theme;
    const tag = new URLSearchParams(this.props.location.search).get("tag");
    const filtered_sections = this.filterSections(customProjects["sections"], customProjects["allowed_tags"], tag);
    // console.log(filtered_sections)
    return (
      <div className="custom_projects-main">
        <Header theme={theme} />
        <div className="basic-custom_projects">
          <Fade bottom duration={2000} distance="40px">
            <div className="custom_projects-heading-div">
              <div className="custom_projects-heading-img-div">
                {/* <img
									src={require(`../../assests/images/${project["header_image_path"]}`)}
									alt=""
								/> */}
                <CustomProjectsImg theme={theme} />
              </div>
              <div className="custom_projects-heading-text-div">
                <h1
                  className="custom_projects-heading-text"
                  style={{ color: theme.text }}
                >
                  {customProjects.title}
                </h1>
                <h3
                  className="custom_projects-heading-sub-text"
                  style={{ color: theme.text }}
                >
                  {customProjects["subtitle"]}
                </h3>
                <p
                  className="custom_projects-header-detail-text subTitle"
                  style={{ color: theme.secondaryText }}
                >
                  {customProjects["description"]}
                </p>
              </div>
            </div>
          </Fade>
        </div>
        <div className="custom_projects-search-div">
          <Select theme={theme}
                menuPortalTarget={document.body} menuPosition={'fixed'}
                className=".custom_projects-search "
                options={customProjects["allowed_tags"]}
                onChange={this.handleChange}
                // onInputChange={this.handleInputChange}
                placeholder="Search by tag..."
                value={this.getDefaultValue(customProjects["allowed_tags"], tag)}
                isClearable={true}
                maxMenuHeight={160}
                // theme={theme}
                styles={{
                  control: (baseStyles, state) => ({
                    ...baseStyles,
                    background: theme.body,
                    // borderColor: state.isFocused ? 'grey' : 'red',
                    // border: state.isFocused ? 0 : 0,
                    border: state.isFocused ? "1px solid rgba(14, 107, 168, 0.467)" : "1px solid rgba(14, 107, 168, 0.467)",
                    boxShadow: state.isFocused ? "0px 0px 6px rgba(14, 107, 168, 0.467)" : "none",
                    "&:hover": {
                      border: "1px solid rgba(14, 107, 168, 0.467)",
                      boxShadow: "0px 0px 6px rgba(14, 107, 168, 0.467)"
                    },
                    color: "rgb(127, 141, 170)"
                  }),
                  option: (styles, { data, isDisabled, isFocused, isSelected }) => {
                    // return {
                    //   ...styles,
                    //   backgroundColor: isDisabled
                    //     ? undefined
                    //     : isSelected
                    //     ? data.color
                    //     : isFocused
                    //     // ? "rgb(127, 141, 170, 0.3)"
                    //     ? "rgb(166, 225, 250, 0.4)"
                    //     : undefined,
                    //   color: "rgb(0, 28, 85)",
                    // }
                    return {
                      ...styles,
                      background: theme.body,
                      color: theme.text,
                    }
                  },
                  noOptionsMessage: provided => ({
                    ...provided,
                    background: theme.body,
                    color: theme.text
                  }),
                  singleValue: provided => ({
                    ...provided,
                    color: theme.text
                  }),
                  input: provided => ({
                    ...provided,
                    color: theme.text
                  }),
                  menu: (base) => ({
                    ...base,
                    background: theme.body
                  }),
                  menuList: (base) => ({
                    ...base,
                
                    "::-webkit-scrollbar": {
                      width: "4px",
                      height: "0px",
                    },
                    // "::-webkit-scrollbar-track": {
                    //   background: "#f1f1f1"
                    // },
                    // "::-webkit-scrollbar-thumb": {
                    //   background: "#888"
                    // },
                    "::-webkit-scrollbar-thumb": {
                      background: "#888"
                    },
                    "::-webkit-scrollbar-thumb:hover": {
                      background: "rgba(0, 28, 85, 0.7)"
                    }
                  })
                }}
              />
              </div>
            
        <ProjectAccordion sections={filtered_sections} theme={theme} />
        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default CustomProjects;
